import React, { Component } from 'react'
import {Form, Icon, Input, Button, notification, message } from 'antd'
import { Redirect } from 'react-router-dom'
import './login.less'
import {connect} from 'react-redux'
import {login, clearLogout} from '../../redux/actions'
const Item = Form.Item // 不能写在import之前
/**
 * 登陆的路由组件
 */
class Login extends Component {
  async handleSubmit(event) {
    event.preventDefault()
    // 对所有表单字段进行检验
    this.props.form.validateFields(async (err, values) => {
      // 检验成功
      if (!err) {
        // console.log('提交登陆的ajax请求', values)
        // 请求登陆
        const { username, password } = values
        // 调用分发异步action的函数 => 发登陆的异步请求, 有了结果后更新状态
        this.props.login(username, password)
      } else {
        console.log('检验失败!')
      }
    })
  }
  render() {
    if (this.props.user && this.props.user.logout) {
      notification.success({
        message: '退出成功',
        duration: 2
      })
      this.props.clearLogout('logout')
    }
    if (this.props.user && this.props.user.errorMsg) {
      message.error(this.props.user.errorMsg)
      this.props.clearLogout('errorMsg')
    }
    if (this.props.user && this.props.user._id) {
      //判断用户已经登陆
      notification.success({
        message: '登录成功',
        duration: 2
      })
      return <Redirect to="/" />
    }
    const { getFieldDecorator } = this.props.form
    return (
      <div className="login">
        <header className="login-header">
        <img
        src="data:image/svg+xml;base64,"
        alt=""
      />
          <h1>切图狗系统</h1>
        </header>
        <section className="login-content">
          <h2>用户登陆</h2>
          <Form onSubmit={this.handleSubmit.bind(this)} className="login-form">
            <Item>
              {/*
            用户名/密码的的合法性要求
              1). 必须输入
              2). 必须大于等于4位
              3). 必须小于等于12位
              4). 必须是英文、数字或下划线组成
             */}
              {getFieldDecorator('username', {
                rules: [
                  { required: true, message: 'Please input your username!' }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />
                  }
                  placeholder="用户名"
                />
              )}
            </Item>
            <Form.Item>
              {getFieldDecorator('password', {
                rules: [
                  { required: true, message: 'Please input your password!' }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
                  }
                  type="password"
                  placeholder="密码"
                />
              )}
            </Form.Item>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
              >
                登陆
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
    )
  }
}
const WrapLogin = Form.create()(Login)
export default connect(
  state => ({user: state.user}),
  {login, clearLogout}
)(WrapLogin)